import '../../frame/frame.js';
import '../center-common.js';
import './system-center.less';

var app = angular.module('systemCenterApp', ['gm.ngCustom.control', 'ngResource']);
app.controller('bannerCtrl', ['$scope', function ($scope) {
    $scope.backgoundImg = GLOBAL_INDEX_CONFIG.backgoundImg;
    $scope.systemName = GLOBAL_INDEX_CONFIG.systemName;
    $scope.EnglishSystemName = GLOBAL_INDEX_CONFIG.EnglishSystemName;
}]);
app.controller('swiperBoxCtrl', ['$scope', '$timeout', function ($scope, $timeout) {
    $scope.swiperList = GLOBAL_INDEX_CONFIG.swiperList;
    $timeout(function () {
        var pWiper = new Swiper('.swiper-container', {
            //autoplay :5000,
            slidesPerView: 4,
            loop: false,
            nextButton: '.swiper-button-next',
            prevButton: '.swiper-button-prev'
        });
        pWiper.onResize();
    }, 10);

    $scope.goSubsystem = function (strUrl, $event) {
        GM.CenterCommon.openSystem(strUrl);
        return false;
    };
}]);

app.run(['$templateCache', function ($templateCache) {
    var strTem = require('./system-center.html');
    $templateCache.put('system-center.tpl.html', strTem);
}]);

//验证token
GM.FrameManager.addToken(app);

angular.bootstrap(document, ['systemCenterApp']);



